<script>
  import Vue from "vue"
  import $ from "jquery"
  import Swiper from "swiper/swiper-bundle.min"
  import 'swiper/swiper-bundle.min.css'
  import HomePageTop from "../common/HomePageTop";
  import ToTop from "../common/ToTop";
  export default {
    name: "Chiefly",
    components: {
      HomePageTop,ToTop
    },
    data() {
      return {
        active: 0,
        maskingdisplay:false,
      }
    },

    methods: {
      sonFun(param){
        this.maskingdisplay=param
      },
      maskingShow(){
        this.maskingdisplay=true
          this.$emit('acquire',true);
      },
      skip() {
        //,,,   (,,)
        let DetermineLogin = this.$store.state.Login;
        if (DetermineLogin == undefined) {
          let log = "1";
          this.$store.commit("logins", log);
          this.$router.push({
            path: "./Register"
          })
          //  undefined
        } else {}
      }
    },
    mounted() {
      var slideshow = new Swiper('.slideshow', {
        direction: 'horizontal', // 
        loop: true, // 
        // 
        pagination: {
          el: '.swiper-pagination',
        },
      });
      // 
      $(document).ready(function() {
        $($(".boxdiv li")[0]).addClass("choose");
        $('.boxdiv').on('scroll', function(e) {
          if ($(this).scrollLeft() === 0) {
            $(this).find("ul").addClass("extrudeLeft");
            $(this).find("ul").removeClass("extrudeRight");

          }
          if (($(this).width() + $(this).scrollLeft()) == Math.floor($('.boxdiv ul').width())) {
            $(this).find("ul").addClass("extrudeRight");
            $(this).find("ul").removeClass("extrudeLeft");
          } else {
            $(this).removeClass("extrudeLeft");
            $(this).removeClass("extrudeRight")
          }
        });
        //
        $(".boxdiv li").click(function() {
          $(this).addClass("choose");
          $(this).siblings().removeClass("choose");
          //
          if ($(this).index() == 3) {
            // $(this).scrollLeft()="192"
            $(".boxdiv").animate({
              scrollLeft: 207 //bodyscrollToppostop
            }, 1000);
          } else if ($(this).index() == 2) {
            $(".boxdiv").animate({
              scrollLeft: 0 //bodyscrollToppostop
            }, 1000);
          }
        })
      });
      //
      function validationHeightNumber(e, num) {
        var regu = /^[0-9]+\.?[0-9]*$/;
        if (e.value != "") {
          if (!regu.test(e.value)) {
            layer.msg('', {
              icon: 5,
              time: 2000
            });
            e.value = e.value.substring(0, e.value.length - 1);
            e.focus();
          } else {
            if (num == 0) {
              if (e.value.indexOf('.') > -1) {
                e.value = e.value.substring(0, e.value.length - 1);
                e.focus();
              }
            }
            if (e.value.indexOf('.') > -1) {
              if (e.value.split('.')[1].length > num) {
                e.value = e.value.substring(0, e.value.length - 1);
                e.focus();
              }
            }
          }
        }
        var BoardHeight = $("#BoardHeight").val();
        var reg = /\s/g;
        var BoardHeightvalue = BoardHeight.replace(reg, "");
        if (BoardHeightvalue > 65) {
          layer.msg('65cm', {
            icon: 5,
            time: 3000
          });
        } else if (BoardHeightvalue == "" || BoardHeightvalue < 0 || isNaN(BoardHeightvalue) || BoardHeightvalue == 0) {
          layer.msg('', {
            icon: 5,
            time: 2000
          });
        }
      }
      function validationWidthNumber(e, num) {
        var regu = /^[0-9]+\.?[0-9]*$/;
        if (e.value != "") {
          if (!regu.test(e.value)) {
            layer.msg('', {
              time: 2000,
              icon: 5
            });
            e.value = e.value.substring(0, e.value.length - 1);
            e.focus();
          } else {
            if (num == 0) {
              if (e.value.indexOf('.') > -1) {
                e.value = e.value.substring(0, e.value.length - 1);
                e.focus();
              }
            }
            if (e.value.indexOf('.') > -1) {
              if (e.value.split('.')[1].length > num) {
                e.value = e.value.substring(0, e.value.length - 1);
                e.focus();
              }
            }
          }
        }
        var BoardWidth = $("#BoardWidth").val();
        var reg = /\s/g;
        var BoardWidthvalue = BoardWidth.replace(reg, "");
        if (BoardWidthvalue > 65) {
          layer.msg('65cm', {
            icon: 5,
            time: 2000
          });
        } else if (BoardWidthvalue == "" || BoardWidthvalue < 0 || isNaN(BoardWidthvalue) || BoardWidthvalue == 0) {
          layer.msg('', {
            icon: 5,
            time: 2000
          });
        }
      }
    },
    created() {




    }
  }
</script>
