<template>
   <div class="dailog">
        <van-popup v-model="show" position="bottom" :style="{ height: '70%' }" >
         <div class="address">
            <!-- <van-picker show-toolbar  @cancel="quit()"  @confirm="AddressConfirm"></van-picker> -->
            <div class="fixed-top">
                <div class="op-btn flex-row">
                    <div class="left" @click="quit()"></div>
                    <div class="rigth cl_f90" @click="AddressConfirm"></div>
                </div>
                <div class="step">
                    <p class="step-item" :class="provice!=''?'steped':''" @click="update(1)">
                        <i class="yuan"></i>
                        <span>{{provice}}</span> 
                    </p>
                    <p class="step-item" @click="update(2)" v-if="provice!=''" :class="city!=''?'steped':''"><i class="yuan"></i>{{city}}</p>
                    <p class="step-item" @click="update(3)" v-if="city!=''"><i class="yuan"></i>{{area}}</p>
                </div>
            </div>
             <ul v-if="provice==''" class="pul">
                 <li v-for="(item,index) in prodata" :key="index" class="city_item flex-row" @click="selP(item,index)" :class="current==index?'cl_f90':''">
                     {{item.value}}  <i class="checked" v-show="current==index"></i>
                 </li>
             </ul>
             <ul v-if="provice!='' && city == ''" class="cul">
                 <li v-for="(item,index) in citydata" :key="index"  class="city_item flex-row" @click="selC(item,index)" :class="current2==index?'cl_f90':''">
                     {{item.value}}  <i class="checked" v-show="current2==index"></i>
                 </li>
             </ul>
             <ul v-if="city!=''" class="aul">
                 <li v-for="(item,index) in areadata" :key="index" class="city_item flex-row" @click="selA(item,index)" :class="current3==index?'cl_f90':''">
                     {{item.value}}  <i class="checked" v-show="current3==index"></i>
                 </li>
             </ul>
         </div>
      </van-popup>
   </div>
</template>
   
<script>
   import Vue from "vue"
   import city from '../../assets/city.json'
    export default {
        name:'.dailog',
        data(){
            return {
               show:false,
               prodata:city.data,
               current:'-1',
               current2:'-1',
               current3:'-1',
               provice:'',  //
               city:'',   //
               area:'', //
               citydata:[],
               areadata:[],
               address:''
            }
        },
        methods:{
          selP(item,index){
            this.current = index;
            this.provice = item.value;
            this.citydata = item.children;    
            if(this.provice == '' || this.provice == '' || this.provice == '' || this.provice == ''){
                this.city = this.provice;
                this.areadata = item.children[0].children; 
            }
          },
          selC(item,index){
                this.current2 = index;
                this.city = item.value;
                this.areadata = item.children; 
          },
          selA(item,index){
                this.current3 = index;
                this.area = item.value;
          },
          update(type){
             if(type == '1'){
                 this.provice = '';
                 this.current = '-1';
                 this.city = '';
             }else if(type == '2'){
                 this.city = '';
                 this.area = '';
                 this.current2 = '-1';
             }else if(type == '3'){
                 this.area = '';
                 this.current3 = '-1';
             }
          },
          selAddress(){
              this.show = true;  
          },
          AddressConfirm(){
             this.address = this.provice + this.city + this.area;
             this.$emit("objxevent",this.address);
             this.quit();
          },
          quit(){
              this.show = false;
              this.current = '-1';
              this.current2 = '-1';
              this.current3 = '-1';
              this.provice = ''; 
              this.city = '';
              this.area = '';
          }
        }
    }
</script>
<style scoped>
   .dailog .op-btn{
       width:100%;
       height: 1.06rem;
       align-items: center;
       justify-content: space-around;
       padding:0 0.3rem;
       font-size: 0.3rem;
       border-bottom:1px solid #E3E4E8;
   }
   .dailog .fixed-top{
        position: fixed;
        top:30%;
        width:100%;
        z-index:99;
        background-color: #fff;
   }
    .dailog .address ul{
        position: relative;
        overflow-y:auto;
        top:0;
    }
     .dailog .address .pul{
         padding-top:2rem;
     }
    .dailog .address .cul{
         padding-top:3rem;
     }
    .dailog .address .aul{
         padding-top:4.3rem;
     }
    .dailog .pull-right{float: right;}
    .dailog .flex-row{
        display:flex;
        flex-direction: row;
        flex-wrap: wrap;
       justify-content: space-between;
    }
    .dailog .cl_f90{color:#f90}
    .dailog .steped{
        position: relative;
    }
    .dailog .steped .yuan{
       background-color: #f90;
    }
    .dailog .steped:before{
        content:'';
        position: absolute;
        width: 0.03rem;
        height: 0.99rem;
        background-color: #ff9900;
        display: block;
        top: 0.44rem;
        left: 0.4rem;
        z-index: 99;
    }
    .dailog .flex-1{flex:1}
    .dailog .checked{
        display: inline-block;
        height: 0.16rem;
        width: 0.28rem;
        border-left:1px solid #f90;
        border-bottom:1px solid #f90;
        transform:rotate(-45deg);
    }
    .dailog .yuan{
        display: inline-block;
        width: 0.22rem;
        height: 0.22rem;
        background-color: #f9fafd;
        border: solid 1px #ff9900;
        border-radius: 50%;
        margin-right:0.1rem;
    }
    
    .dailog .step-item{
        width: 100%;
        height: 1.04rem;
        line-height: 1.04rem;
        padding-left:0.3rem;
        color:#f90;
        font-size: 0.28rem;
        background-color: #fef8f1;
    }
    .dailog .city_item{
        height: 0.92rem;
        line-height:0.92rem;
        padding:0 0.3rem;
        font-size: 0.28rem;
        background-color: #fff;
        align-items: center;
        justify-content: space-between;
    }
</style>


