<template>
    <div class="editAddress">
        <header class="flex-row">
           <i class="iconfont cl_333" @click="back">&#xe682;</i>
           <h3 class="f38 cl_333"></h3>
           <div class="right-btn f28" @click="del()"></div>
        </header>
        <div class="addressContent mt24">
               <div class="fiexoverTitle ">
                    <div class="fiexover border_E3E4E8">
                        <p></p>
                        <div style="height: 100%;line-height: 1.02rem" class="f34 cl_333">
                            <input type="text" style="border: none;text-align: right" placeholder="" v-model="address.name" @change="edit()">
                        </div>
                    </div>
               </div>
                <div class="fiexoverTitle ">
                    <div class="fiexover border_E3E4E8">
                        <p></p>
                        <div style="height: 100%;line-height: 1.02rem" class="f34 cl_333">
                            <input type="text" style="border: none;text-align: right" placeholder="" v-model="address.tel">
                        </div>
                    </div>
               </div>
                <div class="fiexoverTitle ">
                    <div class="fiexover border_E3E4E8">
                        <p></p>
                        <div style="height: 100%;line-height: 1.02rem" class="f34 cl_333" @click="selP()">
                           <input type="text" style="border: none;text-align: right" placeholder="" v-model="address.provice" disabled class="bgfff">
                           <i class="iconfont">&#xe67c;</i>
                        </div>
                    </div>
               </div>
                <div class="fiexoverTitle ">
                    <div class="fiexover border_E3E4E8">
                        <p></p>
                        <div style="height: 100%;line-height: 1.02rem" class="f34 cl_333">
                           <input type="text" style="border: none;text-align: right" placeholder="" v-model="address.address">
                        </div>
                    </div>
               </div>
               <van-cell center title="">
                    <template #right-icon>
                        <van-switch v-model="address.isDefault" size="24" active-color="#f90"/>
                    </template>
                </van-cell>
        </div>
        <div class="save-btn mt24" @click="save()"></div>
        <!--  -->
        <addressDialog ref="child" @objxevent='parentEvent'></addressDialog>
    </div>
</template>
<style scoped>
   .bgfff{background-color: #fff;}
   .save-btn{
       height: 1rem;
       display:block;
       line-height:1rem;
       width:100%;
       color:#f90;
       text-align: center;
       font-size: 0.34rem;
       background-color: #fff;
   }
   header{
      height: 0.92rem;
      background-color: #ffffff;
      align-items: center;
      padding:0 0.3rem;
      box-sizing: border-box;
   }
   header h3{
     font-weight: normal;
   }
</style>
<script>
import addressDialog from '../../dialog/address'
export default {
    name:'.editAddress',
    components:{addressDialog},
    data() {
        return {
           address: {
                id: '',
                name: '',
                tel: '',
                provice:'',
                address: '',
                isDefault: false,  //
            },
        }
    },
    methods:{
        del(){  //
          this.$dialog.confirm({
                message: "",
                showCancelButton:true,
                // confirmButtonText: "", //
                // cancelButtonText: "" //
                    }).then(()=> {
                         this.$router.push({
                            name:'addresslist',
                            params:{
                              id:this.address.id
                            } 
                        })
                    }).catch(() => {
                      
                    })
        },
        selP(){   //
           this.$refs.child.selAddress(); 
        },
        back(){
            this.$router.go(-1);//
        },
       save(){
          if(this.address.name == ''){
              this.$toast({
                    message:'',
                    position:'200px'   // 
              });
              return;
          }
          if(this.address.tel == ''){
              this.$toast({
                    message:'',
                    position:'200px'   // 
              });
              return;
          }
          if(this.address.provice == ''){
              this.$toast({
                    message:'',
                    position:'200px'   // 
              });
              return;
          }
          if(this.address.address == ''){
               this.$toast({
                    message:'',
                    position:'middle'   // 
              });
              return;
          }else{
                this.$store.commit("Daddress",this.address);
                //
                this.$router.replace('/address/list');
          }
       },
       parentEvent(data){          
            this.address.provice = data;
        },
    },
    created(){
       // 
       if(this.$route.params.item){
          this.address = this.$route.params.item;
       }

    }
}
</script>


