<template>
    <div class="EditInformation">
      <HomePageTop :TopTitle="TopTotle" class="bg_fff" ></HomePageTop>

      <div class="fiexoverTitle mb36 mt24"  @click="SelectShow = true">
        <div class="fiexover">
          <p>
            
          </p>
          <div class="cl_333">
            {{SelectText}}
            <i class="iconfont"></i>
          </div>
        </div>
      </div>
      <div class="fiexoverTitle">
        <div class="fiexover border_E3E4E8">
          <p><i style="color: red;vertical-align: middle;display: inline-block;">*</i></p>
          <div style="height: 100%;line-height: 1.02rem" class="f34 cl_333">
            <input type="text" style="border: none;text-align: right" placeholder="" v-model="input1" @input="OnInput" >
<!--            <i class="iconfont op0"></i>-->
          </div>
        </div>
      </div>
      <div class="fiexoverTitle">
        <div class="fiexover border_E3E4E8">
          <p><i style="color: red;vertical-align: middle;display: inline-block;">*</i></p>
          <div style="height: 100%;line-height: 1.02rem" class="f34 cl_333">
            <input type="text" style="border: none;text-align: right" placeholder="15/18/201" @input="OnInput"  v-model="input2"oninput="value=value.replace(/[^\d]/g,'')">
<!--            <i class="iconfont op0"></i>-->
          </div>
        </div>
      </div>
      <div class="fiexoverTitle">
        <div class="fiexover border_E3E4E8">
          <p><i style="color: red;vertical-align: middle;display: inline-block;">*</i></p>
          <div style="height: 100%;line-height: 1.02rem" class="f34 cl_333">
            <input type="text" style="border: none;text-align: right" placeholder="" v-model="input3" @input="OnInput" >
<!--            <i class="iconfont op0"></i>-->
          </div>
        </div>
      </div>
      <div class="fiexoverTitle">
        <div class="fiexover border_E3E4E8">
          <p><i style="color: red;vertical-align: middle;display: inline-block;">*</i></p>
          <div style="height: 100%;line-height: 1.02rem" class="f34 cl_333">
            <input type="text" style="border: none;text-align: right" placeholder="" v-model="input4" @input="OnInput" >
<!--            <i class="iconfont op0"></i>-->
          </div>
        </div>
      </div>
      <div class="fiexoverTitle">
        <div class="fiexover border_E3E4E8">
          <p><i style="color: red;vertical-align: middle;display: inline-block;">*</i></p>
          <div style="height: 100%;line-height: 1.02rem" class="f34 cl_333">
            <input type="text" style="border: none;text-align: right" placeholder="" v-model="input5" @input="OnInput" >
<!--            <i class="iconfont op0"></i>-->
          </div>
        </div>
      </div>
      <div class="fiexoverTitle">
        <div class="fiexover border_E3E4E8">
          <p><i style="color: red;vertical-align: middle;display: inline-block;">*</i></p>
          <div style="height: 100%;line-height: 1.02rem" class="f34 cl_333">
            <input type="text" style="border: none;text-align: right" placeholder="" v-model="input6" @input="OnInput" >
<!--            <i class="iconfont op0"></i>-->
          </div>
        </div>
      </div>
      <div class="public-btn mt60  mr30 ml30" :class="SubmitClassbox==true?'public-btn':'public-btnNo'" @click="SubmitClassClick">
        
      </div>

      <van-action-sheet v-model="SelectShow" :actions="actions" @select="onSelect" :round=false />


    </div>

</template>

<script>
    import Vue from "vue"
    import HomePageTop from "../common/HomePageTop";

    export default {
        name: "EditInformation",
        components: {HomePageTop},
        data() {
            return {
              //
              TopTotle: {name: "",left: true},
              //
              SelectShow: false,
              actions: [{name: ''}, { name: ''}, ],
              SelectText:"",
              //
              input1:"",
              input2:"",
              input3:"",
              input4:"",
              input5:"",
              input6:"",
              SubmitClassbox:false,
              //
              DeliveryIndex:"",
              //
              ThisTrue:"",
            }
        },
      watch:{
        SelectText(){this.OnInput()},
      },
        methods: {
          OnInput(){
            if(this.input1!=""&&this.input2!=""&&this.input3!=""&&this.input4!=""&&this.input5!=""&&this.input6!=""&&this.SelectText!=""){
              this.SubmitClassbox=true
            }

          },
          onSelect(item) {
            // 
            //  close-on-click-action 
            this.SelectShow = false;
            this.SelectText = item.name;
          },
          //
          SubmitClassClick(){
            //
            if(this.SubmitClassbox==true){
              let  PlusTicket;
              //
              // console.log(this.DeliveryIndex,"",this.DeliveryIndex,this.ThisTrue);
              //id1  ,
              if(this.$route.params.id==1){
                //
                PlusTicket={name:"",text:this.input2,danwei:this.input2,kaihuhang:this.input3,zhanghao:this.input4,dizhi:this.input5,dianhua:this.input6,SelectText:this.SelectText,id:this.$route.params.id};
                //vuex
                this.$store.commit("PlusTicket",PlusTicket);
                this.$router.push({
                  name:"DrawerMessage",
                });
                console.log(PlusTicket);
              }else{
                //
                PlusTicket={name:this.ThisTrue,text:this.input2,danwei:this.input2,kaihuhang:this.input3,zhanghao:this.input4,dizhi:this.input5,dianhua:this.input6,OnIndex:this.DeliveryIndex,SelectText:this.SelectText,id:this.$route.params.id};
                this.$store.commit("PlusTicket",PlusTicket);
                this.$router.push({path:"DrawerMessage"});
                console.log(PlusTicket);
              }
            }
          },
        },
        created() {
          console.log(this.$route.params.data);
          console.log(this.$route.params.index);
          console.log(this.$route.params.SelectText);
          console.log(this.$route.params);
          if(this.$route.params.id==1){
            this.SelectText = this.$route.params.SelectText;
            console.log(this.SelectText);
          }else{
            //
            this.ThisTrue=this.$route.params.data.name;
            //
            this.input1=this.$route.params.data.danwei;
            //
            this.input2=this.$route.params.data.text;
            //
            this.input3=this.$route.params.data.kaihuhang;
            //
            this.input4=this.$route.params.data.zhanghao;
            //
            this.input5=this.$route.params.data.dizhi;
            //
            this.input6=this.$route.params.data.dianhua;
            //
            this.DeliveryIndex=this.$route.params.index;
            //
            this.SelectText = this.$route.params.SelectText;
          }
        }
    }

</script>

<style>
  html,
  both {
    background: #F4F6F9;
  }
</style>



